"use client";

import Navbar from './Navbar';

const NavbarWrapper = () => {
  const handleLanguageChange = (language) => {
    console.log('Selected language:', language);
    // Update the base URL based on the selected language
    const newUrl = `/${language}`; // Prepend the language to the path
    window.location.href = newUrl; // Redirect to the new URL
  };

  return <Navbar onLanguageChange={handleLanguageChange} />;
};

export default NavbarWrapper;